header {
    @extend %clearfix;
    background-color: grey;
    position: relative;
    .container--header {
        padding-top: 0px;
        padding-bottom: 0px;
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 1s;
    }
    &#sainsburys,
    &#pbs,
    &#alzheimers,
    &#bp-fll,
    &#icap,
    &#i360,
    &.work-item--monitor {
        min-height: auto;
        height: 100vh;
        .container.container--header {
            position: absolute;
            padding-top: 3%;
        }
    }
    &#page_bp_fll_stories,
    &#page_caroline_lucas,
    &#page_sdnpa,
    &#page_reliace,
    &#page_barclays_values,
    &#page_barclays_integrity,
    &#page_take_the_lead,
    &#page_legacy,
    &#page_leadership,
    &#page_gfk,
    &.work-header-video,
    &#page_discover_bp {
        min-height: 100vh;
        height: 100%;
        position: relative;
        z-index: 1;
    }
    &#page_404 {
        min-height: 100vh;
        height: 100%;
        position: relative;
        z-index: 0;
    }
    #header-play {
        opacity: 0;
        transition: opacity 0.5s ease;
        &.video-ready {
            opacity: 1;
        }
    }
    @keyframes arrow-jump {
        0% {
            transform: translate(0, 0);
        }
        50% {
            transform: translate(0, -10px);
        }
        100% {
            transform: translate(0, 0);
        }
    }
    .scroll-down {
        position: absolute;
        top: 100vh;
        left: 0;
        right: 0;
        text-align: center;
        z-index: 3;
        margin-top: -120px;
        span {
            display: inline-block;
            padding: 20px;
            width: 80px;
            height: 20px;
            text-indent: -9999px;
            animation: arrow-jump 2s infinite;
            &:before,
            &:after {
                content: '';
                position: absolute;
                top: 19px;
                display: block;
                width: 30px;
                height: 1px;
                background: #fff;
            }
            &:before {
                left: 35px;
                transform: rotate(-45deg);
            }
            &:after {
                right: 36px;
                transform: rotate(45deg);
            }
        }
        @media handheld,
        screen and (min-width: $bp-desktop) {
            margin-top: -70px;
            span {
                &:before,
                &:after {
                    width: 50px;
                }
                &:before {
                    left: 32px;
                }
                &:after {
                    right: 33px;
                }
            }
        }
    }
    .header-title {
        h1,
        p {
            transform: translate3d(0, 0, 0);
            transition: all 0.5s ease 0.1s;
            opacity: 0;
        }
        p {
            letter-spacing: 1px;
        }
        h1 {
            line-height: 1;
        }
        h2 {
            transform: translate3d(0, 0, 0);
            transition: all 0.5s ease 0.3s;
            opacity: 0;
        }
        .underlined {
            &:after {
                width: 0px;
                transform: translate3d(0, 0, 0);
                transition: all 0.5s ease 0.5s;
            }
        }
        &+.header-text {
            margin-top: 20px;
            @media handheld,
            screen and (min-width: 0px) and (max-width:$bp-desktop) {
                margin-top: 20px;
            }
        }
        &.no--shadow h1 {
            text-shadow: none;
        }
        &.no--shadow h2 {
            text-shadow: none;
        }
        &.no--shadow p {
            text-shadow: none;
        }
        img.awards {
            position: absolute;
            top: -38px;
            left: 6.25%;
        }
    }
    .header-text {
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 1.9s;
        opacity: 0;
    }
    @media handheld,
    screen and (min-width: $bp-desktop) {
        .header-title {
            &+.header-text {
                margin-top: 30px;
            }
        }
    }
    &.case-study {
        height: 100vh;
        overflow: hidden;
        .container--header {
            position: absolute;
            bottom: percentage(242/2032);
        }
    }
    &.work-item {
        .container--header {
            padding-top: 1%;
            position: fixed;
            bottom: 40%;
            width: 100%;
            background-color: transparent;
        } // .monitor-wrapper+.container--header {
        //     position: relative;
        //     padding-top: 3%;
        // }
        @media handheld,
        screen and (min-width: $bp-desktop) {
            .header-title {
                width: 50%;
                float: left;
            }
        }
    }
    &.work-item {
        .container--reel {
            padding: 0;
            position: absolute;
            bottom: 56%;
            width: 100%;
            text-align: center;
            background-color: transparent;
            p {
                font-family: $f-seroot-blck;
                @include font-size(33);
                @media handheld,
                screen and (min-width: $bp-desktop) {
                    @include font-size(65);
                }
            }
        }
        .header-title {
            width: 50%;
            float: left;
            @media handheld,
            screen and (min-width: 0px) and (max-width: $bp-desktop) {
                width:100%;
            }
        }
    }
    .header-play {
        top: 28%;
        @media handheld,
        screen and (min-width: $bp-desktop) {
            top: 28%;
        }
        @media handheld,
        screen and (min-width: $bp-desktop-large) {
            top: 28%;
        }
    }
    .header-play--casestudy {
        top: 44%;
        @media handheld,
        screen and (min-width: $bp-desktop) {
            top: 44%;
        }
    }
    &.work-item--background-image {
        background-position: top center;
        background-attachment: fixed;
        background-size: cover;
        min-height: 100vh;
        z-index: 1;
    }
    &.work-item--motion,
    &.work-item--background {
        @media handheld,
        screen and (min-width: 0px) and (max-width: $bp-phone) {}
        @media handheld, screen and (min-width:$bp-phone) and (max-width: $bp-desktop) {
            min-height:75vh;
            height: 75%;
        }
        .container--header {
            background-color: $c-grey-1b;
            position: absolute;
            bottom: 0;
            opacity: 0;
            padding: percentage(80/2032);
            &.container--work-list {
                @media handheld,
                screen and (min-width: $bp-desktop) {
                    height: 68px;
                }
                @media handheld,
                screen and (min-width:0px) and (max-width: $bp-desktop) {
                    position: fixed;
                    z-index: 999;
                    bottom: 0;
                }
                margin: 0px auto;
                text-align: center;
                width: 100%;
                height:68px;
                padding: 0;
                opacity: 1 !important;
                transition:none;
            }
            &.sticky {
                position: fixed;
                z-index: 999;
                @media handheld,
                screen and (min-width: $bp-desktop) {
                    top:0;
                }
                @media handheld,
                screen and (min-width:0px) and (max-width: $bp-desktop) {
                    bottom: 0;
                }
            }
        }
        .video-container {
            background-image: url('../images/video-placeholder.jpg');
            background-size: cover;
        }
    }
    .header-text {
        float: left;
        width: 100%;
    }
    @media handheld,
    screen and (min-width: $bp-desktop-large) {
        .header-text {
            float: right;
            width: 50%;
        }
        .header-text__module {
            &+.header-text__module {
                margin-top: 0;
            }
            float: left;
            width: 50%;
        }
    }
    h1+h2 {
        margin-top: 30px;
    }
    &.about-page {
        height: 31.3333%;
        min-height: 400px;
    }
    &#work-page {
        height: 80vh;
        min-height: 80vh;
    }
    .news-wrapper {
        @extend %clearfix;
        background-color: $c-bg;
    }
}

