#work_page {
    .header-play {
        top: 44%;
        @media handheld,
        screen and (min-width: $bp-desktop) {
            top: 44%;
        }
        @media handheld,
        screen and (min-width: $bp-desktop-large) {
            top: 44%;
        }
    }
}

#services--list {
    display: block;
    @media handheld,
    screen and (min-width: $bp-desktop) {
        display:block;
    }
}

.header-play {
    height: 85px;
    width: 85px;
    background-image: url('../images/play_button_v3.png');
    position: absolute;
    z-index: 6;
    cursor: pointer;
    left: 0px;
    right: 0px;
    margin: 0px auto;
}

.header-text__module {
    padding: 0%;
    @media handheld,
    screen and (min-width: $bp-desktop-large) {
        padding: 0% 0% 0% 5%;
    }
    &+.header-text__module {
        margin-top: 5%;
        @media handheld,
        screen and (min-width: $bp-desktop-large) {
            margin-top: percentage(65/768);
        }
    }
}

.loaded {
    .container--header {
        transform: translate3d(0, 0, 0);
        opacity: 0.9 !important;
    }
    .header-title {
        h1,
        p,
        h2 {
            transform: translate3d(0, 0, 0);
            opacity: 1;
        }
        .underlined {
            &:after {
                transform: translate3d(0, 0, 0);
                width: em(150, 75);
            }
        }
    }
    .news-container .intro-text {
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 0.4s;
        opacity: 1;
    }
    .news-container .header-title h1,
    .news-container .header-title p.tag {
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 0.1s;
        opacity: 1;
    }
    .news-container .header-title h1 {
        &:after {
            transform: translate3d(0, 0, 0);
            transition: all 0.5s ease 0.7s;
            opacity: 1;
        }
    }
    .header-text {
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 0.2s;
        opacity: 1;
    }
    .intro-text,
    h2.news-date {
        transform: translate3d(0, 0, 0);
        transition: all 0.5s ease 0.2s;
        opacity: 1;
    }
}

#header-overlay {
    background: -moz-linear-gradient(top, rgba(31, 31, 31, 0.8) 0%, rgba(31, 31, 31, 1) 32%, rgba(31, 31, 31, 1) 100%);
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(31, 31, 31, 0.8)), color-stop(32%, rgba(31, 31, 31, 1)), color-stop(100%, rgba(31, 31, 31, 1)));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, rgba(31, 31, 31, 0.8) 0%, rgba(31, 31, 31, 1) 32%, rgba(31, 31, 31, 1) 100%);
    /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, rgba(31, 31, 31, 0.8) 0%, rgba(31, 31, 31, 1) 32%, rgba(31, 31, 31, 1) 100%);
    /* Opera 11.10+ */
    background: -ms-linear-gradient(top, rgba(31, 31, 31, 0.8) 0%, rgba(31, 31, 31, 1) 32%, rgba(31, 31, 31, 1) 100%);
    /* IE10+ */
    background: linear-gradient(to bottom, rgba(31, 31, 31, 0.8) 0%, rgba(31, 31, 31, 1) 32%, rgba(31, 31, 31, 1) 100%);
    /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc1f1f1f', endColorstr='#1f1f1f', GradientType=0);
    /* IE6-9 */
    opacity: 1 !important;
    @media handheld,
    screen and (min-width: $bp-desktop) {
        margin-top: -14vh;
    }
    &.container {
        padding: 15% 17% 12% 15%;
        @media handheld,
        screen and (min-width: 0px) and (max-width:$bp-phone) {
            padding:15% 9% 12% 9%;
        }
    }
    .text-container {
        top: auto;
        position: relative;
        transform: none;
    }
}

#home-page {
    height: 100vh;
