    visibility: visible;
}

.vjs-user-inactive img#vjs-logobrand-image {
    visibility: hidden;
}

.vjs-user-inactive.vjs-paused img#vjs-logobrand-image {
    visibility: visible;
}

.vjs-ad-playing a#vjs-logobrand-image-destination {
    display: none;
}

.monitor-screen .vjs-default-skin .vjs-big-play-button {
    top: -50%;
    transform: translateY(-33%);
    ;
}

#video-overlay .vjs-default-skin .vjs-fullscreen-control {
    display: none;
}

.circle-container {
    margin: 0px auto;
    position: relative;
}

.circle {
    border-radius: 60px;
    width: 60px;
    height: 60px;
    background-color: #ffffff;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0px auto;
    margin-top: 20px;
    opacity: 0.6;
    -webkit-animation: circleBounce 2.0s infinite ease-in-out;
    animation: circleBounce 2.0s infinite ease-in-out;
}

.circle--2 {
    background-color: #ffffff;
    opacity: 0.6;
    z-index: 2;
    animation-delay: -1s;
}

@keyframes circleBounce {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
    50% {
        transform: scale(1);
        -web-kit-transform: scale(1);
    }
    100% {
        transform: scale(0);
        -web-kit-transform: scale(0);
    }
}

@-webkit-keyframes circleBounce {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
    50% {
        transform: scale(1);
        -web-kit-transform: scale(1);
    }
    100% {
        transform: scale(0);
        -web-kit-transform: scale(0);
    }
}

.vjs-waiting .vjs-loading-spinner {
    animation: none;
    -webkit-animation: none;
    -moz-animation: none;
    -o-animation: none;
    position: absolute;
    width: auto;
    height: auto;
    opacity: 1;
    top: 50%;
    -webkit-transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0px auto;
    margin-top: -54px;
    .work-item--motion & {
        top: 33%;
    }
    .work-item--background & {
        top: 33%;
    }
    &:before {
        display: none;
    }
}

#work_page .vjs-loading-spinner {
    top: 50%;
}

.vjs-progress-control {
    opacity: 0;
    transition: opacity 0.5s ease;
}

#overlay-video,
.module--video {
    &:hover {
        .vjs-progress-control {
            opacity: 1;
        }
    }
}