.area-dark {
	background-color: $c-bg;
	h1,
	h2,
	h3,
	h4,
	h5,
	p,
	span,
	li {
		color: white;
	}
	header.case-study & {
		background: none;
	}
	.wrapper .container--mobile-careers & {
		@media handheld,
		screen and (min-width: 0px) and (max-width: $bp-desktop) {
			background-color:#252525;
		}
	}
}

.container.area-dark {
	background-color: $c-bg;
}

.area-dark--lighter {
	background-color: $c-grey-25;
}

.container.area-blue {
	background-color: #79b6e1;
	color: white;
	h2 {
		color: inherit;
	}
}

.container.container--no-padding-bottom {
	padding-bottom: 0;
	+.container-headline-text {
		padding-top: 6.2007874016%;
	}
}

.centre-image {
	display: block;
	margin: 0px auto;
}

.no-background {
	background-color: transparent;
}

.centre-vertical {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
}

.text-center {
	text-align: center;
}

.area-dark-smoke {
	background: url('../images/about/smoke_bg.jpg') 0px 0px $c-bg no-repeat;
	h1,
	h2,
	h3,
	h4,
	h5,
	p,
	span,
	li {
		color: white;
	}
	header.case-study & {
		background: none;
	}
}

