        padding: 90px 6.25%;
    }
    .related-container {
        display: block;
        padding: 60px 0 60px 0;
        @media screen and (min-width: $bp-desktop) {
        display: flex;
        padding: 60px 0 60px 0;
        }
        .related-titles {
            position: absolute;
            bottom: 40%;
            padding: 0 50px;
            @media screen and (min-width: $bp-desktop) {
            opacity: 0;
            }
            p{
                font-size: 25px;
                font-family: $f-seroot;
                letter-spacing: 1px;
                text-transform: uppercase;
            }
        }
        .left-project {
            @media screen and (min-width: $bp-desktop) {
                width: 50%;
            }
            position: relative;
            a {
                width: 100%;
