        content: '';
        font-size: 40px;
        padding: 18px;
        font-family: $f-seroot-lt;
        line-height: 39px;
    }
}
.arrow-left{
    display: block;
    position: absolute;
    top: 45%;
    left: 20px;
    z-index: 999999;
    border-radius: 50%;
    padding: 0;
    width: 45px;
    height: 45px;
    background: rgba(0,0,0,0.3);
